.problems {
    padding-bottom: 125px;
    @media screen and(min-width:$desktop){
        padding-bottom: 160px;
    }

    @media screen and (min-width: $tablet){
        padding-bottom: 170px;
    }

    .problems-color {
        background-color: $dark-back;
        width: 100%;
        position: relative;
    }

    .problems-heading {
        font-family: Montserrat;
        font-style: normal;
        font-weight: 800;
        font-size: 20px;
        line-height: 1.36;
        letter-spacing: 0.03em;
        color: $white;
        width: 100%;
        padding-top: 50px;
        padding-bottom: 240px;

        @media screen and (min-width: $tablet){
            font-size: 26px;
            line-height: 130%;

            padding-bottom: 140px;
        }

        @media screen and (min-width:$desktop){
            font-size: 46px;
            padding-bottom: 120px;
        }

        .problems-orang {
            color: $accent-color;
        }
    }
}

.problems-content{
    position: relative;
}

.overflovewd-container {
    position: absolute;
    z-index: 2;
    background-color: $white;
    height: 100%;
    bottom: -38%;
    left: 50%;
    transform: translateX(-50%);
    width: 280px;
    @media screen and(min-width:$tablet){
        width: 688px;
        bottom: -50%;
    }

    @media screen and(min-width:$desktop){
        width: $desktop;
        bottom: -65%;
    }

    transform: translateX(-50%);

}

.problems-list {
    background-color: $white;
    padding: 20px 0;
    padding-left: 30px;
    padding-right: 14px;

    box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);

    counter-reset: num;

    @media screen and (min-width:$tablet){
        display: flex;
        flex-wrap: wrap;
        padding: 30px 0;
        padding-left: 96px;
        padding-right: 80px;
    }

    @media screen and (min-width:$desktop){
        padding: 70px 0;
        padding-left: 114px;
        padding-right: 114px;
    }
 
}

.problem-list-item {

    &:not(:last-child){
        margin-bottom: 10px;
    }

    @media screen and (min-width: $tablet){
        width: calc((100% - 22px)/2);

        &:not(:last-child){
            margin-bottom: 0;
        }

        &:nth-child(odd){
            margin-right: 22px;
        }

        &:not(:nth-last-child(-n+2)){
            margin-bottom: 30px;
        }
     
    }

    @media screen and(min-width: $desktop){
        width: calc((100% - 2*40px)/3);

        &:nth-child(odd){
            margin-right: 0;
        }
        &:not(:nth-last-child(-n+2)){
            margin-bottom: 0;
        }

        &:not(:nth-child(3n)){
            margin-right: 40px;
        }

        &:not(:nth-last-child(-n + 3)){
            margin-bottom: 40px;
        }

    }
}

/*Цифири и линии*/

.problems-list .problem-list-item {
    counter-increment: num;
    position: relative;
}

.problems-list .problem-list-item:before {
    font-weight: bold;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08em;

    color: $accent-color;
    width: 20px;
    height: 2px;
    display: block;
    transform: translateY(11px);

    @media screen and (min-width: $tablet){
        transform: translateY(13px);

    }


}

.problems-list .problem-list-item:nth-last-child(-n + 6) :before {
    content: '0'counter(num);
    display: flex;
    color: $accent-color;
    transform: translateX(12%);
}




.problems-list-line-stroca {

    font-weight: bold;
    font-size: 14px;
    line-height: 143%; 
    
    letter-spacing: 0.08em;
    
    color: #111518;

    @media screen and (min-width: $tablet){
        line-height: 171%;
    }

    @media screen and (min-width: $desktop){
        font-size: 20px;
        line-height: 120%;
    }
}


.problems-img{
    display: none;
    @media screen and (min-width: $desktop){
        fill: #3D3D3D;
        position: absolute;
        display: block;
        bottom: 0;
        right: -350px;
        height:300px;
        z-index: 2;
    }
}

